@import "src/colors.scss";

main {
  display: flex;
  flex-direction: column;
  align-items: center;
}
h2{
  font-family: 'Syne';
    font-weight: 700;
    font-size: 40px;
    color:$second;
}

.ServicesContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;

}


.container {
  overflow: hidden;
}

.filterDiv {
  float: left;
  background-color: #2196F3;
  color: #ffffff;
  text-align: center;
  margin: 2px;
  display: block;
  /* Hidden by default */
}

/* La classe "show" est ajoutée aux éléments filtrés */
.show {
  display: none;
}

/* Style the buttons */

.modal-container {
  display: none;
  position: fixed;
  width: 100%;
  height: 100%;

}

.modal-container.active {
  display: flex;
}

.overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #333333d3;
}

.modal {
  max-width: 700px;
  min-width: 300px;
  min-height: 300px;
  max-height: 70%;
  background: white;
  border-radius: 20px;
  position: fixed;
  z-index: 99;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 30px 100px 30px 100px;
  overflow: auto;

}

.close-modal {
  padding: 10px 10px;
  border: none;
  font-size: 24px;
  position: sticky;
  top: 15px;
  right: 15px;
  cursor: pointer;
  color: black;
  background-color: white;
}
.desactiv {
  display: none;
}

.activ {
  display: flex;
}